import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Divider from '@material-ui/core/Divider';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import Input from '@material-ui/core/Input';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
/*import { Container, Row, Col } from 'reactstrap';*/


import { StyleSheet, View, Alert } from 'react';

import './pdoro.css';


import color from '@material-ui/core/colors/cyan';

class Pomo extends React.Component{
    constructor(props){
        super(props);
        this.state={
            Break:5,
            Session:25,
            RemTime:1500,
            Min:25,
            Sec:'00',
            isOn:true,
            flag:0,
            flag2:0,
            label:'Session',
            count:0
        }

        

        this.handleClick=this.handleClick.bind(this);
        this.reset=this.reset.bind(this);
        this.startTimer=this.startTimer.bind(this);
        this.stopTimer=this.stopTimer.bind(this);
        this.break_dec=this.break_dec.bind(this);
        this.breakInc=this.breakInc.bind(this);
        this.session_dec=this.session_dec.bind(this);
        this.session_inc=this.session_inc.bind(this);
    }

    handleClick(e){
        
    }

    breakInc(e){
        if(this.state.label == 'Break'){
            if(this.state.Break>8){
                if((this.state.flag == 0)){
                    this.setState({
                        Break:this.state.Break+1,
                        Min:this.state.Break+1,
                        RemTime:Math.floor((this.state.Break+1)*60),
                        Sec:'00'
                    })
                }
            }
            else{
                if((this.state.flag == 0)){
                    this.setState({
                        Break:this.state.Break+1,
                        Min:'0'+(this.state.Break+1),
                        RemTime:Math.floor((this.state.Break+1)*60),
                        Sec:'00'
                    })
                }
            }  
        }
        else{
            if((this.state.flag == 0)){
                this.setState({
                    Break:this.state.Break+1,
                })
            }
        }
    }

    break_dec(e){
        if(this.state.label == 'Break'){
            if(this.state.Break>10){
                if((this.state.flag == 0)&&(this.state.Break>1)){
                    this.setState({
                        Break:this.state.Break-1,
                        Min:this.state.Break-1,
                        RemTime:Math.floor((this.state.Break-1)*60),
                        Sec:'00'
                    })
                }
            }
            else{
                if((this.state.flag == 0)&&(this.state.Break>1)){
                    this.setState({
                        Break:this.state.Break-1,
                        Min:'0'+(this.state.Break-1),
                        RemTime:Math.floor((this.state.Break-1)*60),
                        Sec:'00'
                    })
                }
            }
            
        }
        else{
            if((this.state.flag == 0)&&(this.state.Break>1)){
                this.setState({
                    Break:this.state.Break-1
                })
            }
        }
        
    }

    session_inc(e){
        if((this.state.label == 'Session')){
            if(this.state.Session>8){
                if((this.state.flag == 0)){
                    this.setState({
                        Session:this.state.Session+1,
                        Min:this.state.Session+1,
                        RemTime:Math.floor((this.state.Session+1)*60),
                        Sec:'00'
                    })
                }
            }
            else{
                if((this.state.flag == 0)){
                    this.setState({
                        Session:this.state.Session+1,
                        Min:'0'+(this.state.Session+1),
                        RemTime:Math.floor((this.state.Session+1)*60),
                        Sec:'00'
                    })
                }
            }
        }
        else{
            if((this.state.flag == 0)){
                this.setState({
                    Session:this.state.Session+1,
                })
            }
        }
        
    }

    session_dec(e){
        let Min1;
        
        

        if((this.state.label == 'Session')){
            if(this.state.Session>10){
                if((this.state.flag == 0)&&(this.state.Session>1)){
                    this.setState({
                        Session:this.state.Session-1,
                        Min:this.state.Session-1,
                        RemTime:Math.floor((this.state.Session-1)*60),
                        Sec:'00'
                    })
                }
            }
            else{
                if((this.state.flag == 0)&&(this.state.Session>1)){
                    this.setState({
                        Session:this.state.Session-1,
                        Min:'0'+(this.state.Session-1),
                        RemTime:Math.floor((this.state.Session-1)*60),
                        Sec:'00'
                    })
                }
            }
            
            
        }
        else{
            if((this.state.flag == 0)&&(this.state.Session>1)){
                this.setState({
                    Session:this.state.Session-1,
                })
            }
        }

    }
    

    reset(e){
        if((this.state.flag == 0)){
            this.setState({
                Session:25,
                Break:5,
                Min:25,
                Sec:'00',
                label:'Session',
                RemTime:Math.floor(this.state.Session*60)
            })
        }
    }


    startTimer(idd){
        let RemMin,RemSec,Min1,Sec1;

        if(idd == 'play'){
            this.setState({
                count:this.state.count+1
            })
        }

        if(this.state.count==2){
            this.setState({
                flag2:1
            })
        }

        if(((this.state.count)<=2)&&(this.state.flag2 == 1)){

            if((this.state.RemTime == 0)&&(this.state.label == 'Session')){
                this.setState({
                    label:'Break',
                    RemTime:Math.floor(this.state.Break*60)
                })
            }
            else if((this.state.RemTime == 0)&&(this.state.label == 'Break')){
                this.setState({
                    label:'Session',
                    RemTime:Math.floor(this.state.Session*60)
                })
            }

            if((this.state.isOn == false)&&(idd == 'play')){
                this.setState({
                    isOn:true
                })
            }
    
            if(this.state.isOn){
                
                 RemMin = Math.floor(this.state.RemTime/60);
                 RemSec = this.state.RemTime%60;
                 Min1 = Math.floor(RemMin/10);
                 Sec1 = Math.floor(RemSec/10);
    
                 if(Min1 == 0){
                    RemMin = "0"+RemMin;
                 }
    
                 if(Sec1 == 0){
                    RemSec = "0"+RemSec;
                 }
        
                this.setState({
                    Min:RemMin,
                    Sec:RemSec,
                    flag:1
                })
        
                if((this.state.RemTime > 0)){
                    this.setState({
                        RemTime:this.state.RemTime-1
                    })
                    setTimeout((this.startTimer), 1000);
                }
            }
    
            

        }
        else{
            this.setState({
                isOn:false,
                flag:0,
                count:0,
                flag2:1
            })
        }

       

    }

    stopTimer(e){
        this.setState({
            isOn:false,
            flag:0,
            count:0,
            flag2:1
        })
    }

    render(){

        const ses = {
            root:{
                marginTop:'18px'
            }
        }

        return(
            <div id="Pomo_styling">
            <div id="Main_Container">

                <div id="HeadStyle">PomoDoro Clock</div> 
                   
                    <div id="Container">
                    
                        <div >
                            <Grid container spacing={0}>
                                <Grid item xs className="borderc">Break_Length</Grid>
                                <Grid item xs className="borderc">Session_Length</Grid>
                            </Grid>
                            <Grid container spacing={0}>
                                <Grid item xs className="borderc">
                                    <button className="btn" onClick={this.break_dec}><i class="fas fa-arrow-circle-down fa-2x"></i></button>
                                    { " " }{this.state.Break}{ "  " }
                                    <button className="btn" onClick={this.breakInc}><i class="fas fa-arrow-circle-up fa-2x"></i></button>
                                </Grid>
                                <Grid item xs className="borderc">
                                    <button className="btn" onClick={this.session_dec}><i class="fas fa-arrow-circle-down fa-2x"></i></button>
                                    { " " }{this.state.Session}{ "  " }
                                    <button className="btn" onClick={this.session_inc}><i class="fas fa-arrow-circle-up fa-2x"></i></button> 
                                </Grid>
                            </Grid>
                        </div> 
                        
                        <div  className="borderc" id="Sess2">
                            <Grid container spacing={0}>
                                <Grid item xs ></Grid>
                                <Grid item xs={6}  id="Session" >
                                    <div className="ses_pad" style={ses.root}>{this.state.label}</div>
                                    <div className="ses_pad" id="timer">{this.state.Min}:{this.state.Sec}</div>
                                </Grid>
                                <Grid item xs ></Grid>
                            </Grid>
                        </div>
                        <div>
                            <button type="button" className="btn" id='play' value='play' onClick={(e) => this.startTimer('play')}  >
                                <i class="fas fa-play-circle fa-2x"></i>
                            </button>
                            <button type="button" className="btn" id='pause' value='pause' onClick={(e) => this.stopTimer(e)}  >
                                <i class="fas fa-pause-circle fa-2x"></i>
                            </button>
                            <button type="button" className="btn" id="reset" value="reset" onClick={this.reset}>
                                <i class="fas fa-sync fa-2x"></i>
                            </button>
                        </div>
                    </div>

                </div>
                <div id="footer">

                        "Designed and coded by:"  <br></br>

                        <a target="_blank" href="#">  Arup Dawn</a>
                    
                </div> 
            </div>
            
        );

    }
}

export default Pomo;


